g-dividor{border-color:inherit;border-top:1px solid;margin-bottom:2rem}.user-input-container{margin:auto;max-width:400px}.user-form-container{padding:10px 20px 20px 20px;background-color:#1d1d1d;color:#ececec;border-radius:10px;margin-bottom:20px}.res-error{background-color:#ff8d8d;color:#920000;border-radius:5px;padding:2px 5px 2px 5px;margin:5px 0px 15px 0px}.error-div,.success-div{border-radius:5px;padding:7px 15px 2px 15px;margin-bottom:20px}.error-div .button-info,.success-div .button-info{float:right;background-color:Transparent;background-repeat:no-repeat;border:none;cursor:pointer;overflow:hidden;outline:none;display:inline-flex;align-items:flex-start;font-size:2rem;line-height:25px}.error-div h3,.success-div h3{display:flex}.error-div p,.success-div p{margin-bottom:0.5rem}.error-div{background-color:#ff8d8d;color:#920000}.error-div .button-info{color:#920000}.success-div{background-color:#77ffb7;color:#00a035}.success-div .button-info{color:#00a035}.list-header{text-align:left;padding-left:12%;margin-bottom:0px}.list-elements{list-style-position:inside;text-align:left;padding-left:12%}

.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 25px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 7px;
  left: 0;
  right: 0;
  bottom: -7px;
  background-color: #ccc;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: '';
  height: 19.5px;
  width: 19.5px;
  left: 4px;
  bottom: 3px;
  background-color: #3f3f3f;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #3f3f3f;
}

input:checked + .slider:before {
  transform: translateX(22px);
  background-color: #ccc;
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.sliderholder {
  align-content: flex-end;
  display: flex;
  flex-direction: row;
}

.themefont {
  color: #ececec80;
  margin-bottom: 0;
  align-self: center;
}

